import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'emoji-categories',
  styleUrls: ['../styles/emoji-categories.css'],
  template: `
  <ng-container *ngFor="let category of emojisCategories">
    <emoji-button
      (selection)="handleCategorySelection($event)"
      [dataToEmit]="category"
      [emoji]="category.icon"></emoji-button>
  </ng-container>
  `
})

export class EmojiCategoriesComponent {
  @Input('emojisCategories') public emojisCategories: any;
  @Output('categorySelection') public categorySelection = new EventEmitter<any>();

  public handleCategorySelection(event: any) {
    this.categorySelection.emit(event);
  }
}
